<template>
    <section>
        <tg-list v-show="IscrewView" :IsShowAction="true" ModuleName="FSM" SubModuleCode="FSM_Detail" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]" IdentityColumn="sla_id" :SearchFields="leftSearchFields"
            :HeaderText="$t('SLA')" :listType="['List']" :ListData="SLAList" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchRecords"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <listing-status :isfor="'policy'" v-if="isCRMShowStausDrop(data.row.status_id, 'Ticketing')"
                        ref="updatestatus" v-bind:dataid="data.row.sla_id" data-controller="Crm"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.status_id" data-action="index"
                        v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchRecords" v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'policy_name'">
                <RouterLink :to="'/Ticketing/addpolicy/'+data.row.sla_id" :title="data.row[data.column.COLUMN_NAME]"  data-toggle-tooltip="tooltip" data-original-title="Click here to edit Ticketing Policy">
                   {{ data.row[data.column.COLUMN_NAME] }}
                </RouterLink>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'policy_date'">
                    <span>
                        {{ $options.filters.formatDate(data.row.policy_date) }}
                    </span>
                </template>
            </template>
        </tg-list>
    </section>
</template>
<script>
import Schema from "@/views/TicketingPolicy/TicketingPolicySchema";
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            IscrewView: true,
            showSetupTickectSource: false,
            showExpanded: '',
            StatusMappingList: [],
            noRecords: false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            SLAList: [],
            GroupStart: 1,
            GroupEnd: 3,
            ticketSourceId: 0,
            Headers: [{
                "COLUMN_NAME": "policy_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Policy Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "policy_date",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Policy Date",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ORDER_LEVEL",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Order Level",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3
            }
            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            user_type: null,
            user_id: null,
            companyId: null,
            search: '',
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchRecords();
        vm.EnableDisableButtons();
    },
    methods: {
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { 
                result[key] = value;
            }
            return result;
        },
        UPDownDisplay: function (event, status) {
            var vm = this;
            let filteredUsers = vm.SLAList.findIndex((item) => {
                return item.sla_id === event.sla_id;
            });
            if (status == "UPArrow") {
                var thisvalue = event.sla_id;
                var valueAtIndex1 = vm.SLAList.at(--filteredUsers);
                var thisdis_ord = event.ORDER_LEVEL;
                var nextvalue = valueAtIndex1.sla_id;
                var nextdis_ord = valueAtIndex1.ORDER_LEVEL;
                let index = vm.SLAList.findIndex((item) => {
                    return item.sla_id === event.sla_id;
                });
                const temp = vm.SLAList[index]
                vm.SLAList.splice(index, 1)
                vm.SLAList.splice(index - 1, 0, temp);
                var postJSON = {
                    id: thisvalue,
                    idc: nextvalue,
                    disorder: thisdis_ord,
                    disordc: nextdis_ord,
                    type: 'up'
                };
                var objectPostString = JSON.stringify(postJSON);
                DataServices.MovePolicyDisplayOrder(objectPostString).then(function (response) {
                    if (response.data.status) {
                        vm.ShowAlert(vm.$t("RecordMovedSuccessfully", [vm.$t("")]), "success", true, vm.$t("Alert"));
                        vm.FetchRecords();
                    }
                });
            } else if (status == "DownArrow") {
                var thisvalue = event.sla_id;
                var valueAtIndex1 = vm.SLAList.at(++filteredUsers);
                var thisdis_ord = event.ORDER_LEVEL;
                var nextvalue = valueAtIndex1.sla_id;
                var nextdis_ord = valueAtIndex1.ORDER_LEVEL;
                let index = vm.SLAList.findIndex((item) => {
                    return item.sla_id === event.sla_id;
                });
                const temp = vm.SLAList[index]
                vm.SLAList.splice(index, 1)
                vm.SLAList.splice(index + 1, 0, temp);
                var postJSON = {
                    id: thisvalue,
                    idc: nextvalue,
                    disorder: thisdis_ord,
                    disordc: nextdis_ord,
                    type: 'down'
                };
                var objectPostString = JSON.stringify(postJSON);
                DataServices.MovePolicyDisplayOrder(objectPostString).then(function (response) {
                    if (response.data.status) {
                        vm.ShowAlert(vm.$t("RecordMovedSuccessfully", [vm.$t("")]), "success", true, vm.$t("Alert"));
                        vm.FetchRecords();
                    }
                });
            }
            vm.EnableDisableButtons();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'UpOrder':
                        action.isVisible = true;
                        break;
                    case 'DownOrder':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'UpOrder':
                    this.UPDownDisplay(item, 'UPArrow')
                    break;
                case 'DownOrder':
                    this.UPDownDisplay(item, 'DownArrow');
                    break;
            }
        },
        EnableDisableButtons: function () {
            $('.UpDisplayOrder,.DownDisplayOrder').show();
            $('.UpDisplayOrder').first().hide();
            $('.DownDisplayOrder').last().hide();
        },
        leftCommonSearch: function (value) {
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('search')) {
                this.search = params['search'];
            }
            else {
                this.search = '';
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchRecords();
        },
        UpdateStatus: async function (status) {
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmUpdateStatusRecord"), true, (vm.$t("Active") + "  " + vm.$t("Inactive")), function () {
                    var url = `id=${val}&statusId=${status}&isFor=${'policy'}`;
                    DataServices.updatestatus(url).then(response => {
                        if (response.data == true) {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.listheaderbuttons[2].isdisabled=true;
                            vm.listheaderbuttons[3].isdisabled=true;
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                            vm.FetchRecords();
                        }
                    })
                })
            }
        },
        FetchRecords: async function () {
            var vueObj = this;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            if (!vueObj.isBlank(this.search)) {
                url += `&search=${this.search}`;
            }
            await DataServices.GetPolicyListSLA(url).then(function (response) {
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'status_id') {
                        column.DISPLAY_NAME = 'Status'
                        column.settings = {
                            isInSlot: true,
                            isForTag: true
                        }
                    }
                    if (column.COLUMN_NAME == "policy_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == 'policy_date') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                });
                vueObj.SLAList = [];
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                vueObj.TotalRecords = 0;
                if (response.data != null && response.data != "") {
                    if (response.statusText == "OK" && response.status == 200) {
                        vueObj.SLAList = response.data;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        if (vueObj.SLAList.length > 0) {
                            vueObj.TotalRecords = response.data[0].TOTALRECORDS;
                            vueObj.listheaderbuttons[1].isdisabled=true;
                            vueObj.listheaderbuttons[2].isdisabled=true;
                            vueObj.listheaderbuttons[3].isdisabled=true;
                            vueObj.noRecords = false;
                        }
                        else {
                            vueObj.noRecords = true;
                        }
                    }
                    else {
                        vueObj.noRecords = true;
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                }
            });
        },
        pagerMethod: async function (value) {
            var vm = this;
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await vm.FetchRecords();
            vm.EnableDisableButtons();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openaddTicketPolicy":
                    this.openAddEditTicketPolicy(0);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "openaddWorkType":
                    this.openPopupForSetupCrewList(null);
                    break;
                case "updateStatus":
                    this.UpdateStatus(event.id);
                    break;
            }
        },
        openAddEditTicketPolicy(ths) {
            this.$router.push({ name: "addpolicy", params: { id: ths } });
        },
        sortdata: async function (sortBy) {
            var vm = this;
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            await vm.FetchRecords();
            vm.EnableDisableButtons();
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Crew")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_TICKET_SLA"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.listheaderbuttons[2].isdisabled=true;
                            vm.listheaderbuttons[3].isdisabled=true;
                            vm.FetchRecords();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('SLA')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('SLA')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        }
    }
}
</script>
